<template>
  <Dropdown
    ref="dropdown"
    v-model="value"
    :pt="preset"
    :options="filteredOptions"
    :editable="props.editable"
    option-label="label"
    option-value="value"
    @focus="show"
  />
</template>

<script setup lang="ts" generic="ModelValue">
import Dropdown, { type DropdownProps } from "primevue/dropdown"

export type DropdownOption<T> = {
  label: string
  value: T
}

const props = defineProps<{
  options: DropdownOption<ModelValue>[]
  editable?: boolean
}>()

const value = defineModel<ModelValue>({
  get(value) {
    return value || null
  },
})

const dropdown = ref<InstanceType<typeof Dropdown>>()

const filteredOptions = computed(() => {
  if (!props.editable || !value.value || typeof value.value !== "string") {
    return props.options
  }

  const filter = (value.value as string).trim().toLowerCase()
  return props.options.filter(({ label }) => label.toLowerCase().includes(filter))
})

watch(filteredOptions, async () => {
  if (!dropdown.value) {
    return
  }

  if ("alignOverlay" in dropdown.value && typeof dropdown.value.alignOverlay === "function") {
    await nextTick()
    dropdown.value.alignOverlay()
  }
})

function show(e: Event) {
  const target = e.target as HTMLElement
  if (target.tagName === "INPUT") {
    dropdown.value?.show()
  }
}

const preset: DropdownProps["pt"] = {
  root: ({ props, state }) => ({
    class: [
      // Display and Position
      "inline-flex",
      "relative",

      // Shape
      "rounded-sm",

      // Color and Background
      "bg-gray-50 dark:bg-gray-600",
      "border",
      { "border-gray-200 dark:border-gray-400": !props.invalid && !state.focused },

      // Invalid State
      { "border-red-500 dark:border-red-400": props.invalid },

      // Transitions
      "transition-all",

      // States
      "outline-none",
      { "border-blue-700 ": state.focused },

      // Misc
      "cursor-pointer",
      "select-none",
      { "opacity-60": props.disabled, "pointer-events-none": props.disabled, "cursor-default": props.disabled },
    ],
  }),
  input: ({ props }) => ({
    class: [
      //Font
      "font-sans",

      // Display
      "block",
      "flex-auto",

      // Color and Background
      "bg-transparent",
      "border-0",
      {
        "text-surface-800 dark:text-white/80": props.modelValue != undefined,
        "text-surface-400 dark:text-surface-500": props.modelValue == undefined,
      },
      "placeholder:text-surface-400 dark:placeholder:text-surface-500",

      // Sizing and Spacing
      "w-full",
      "p-2",
      { "pr-7": props.showClear },

      //Shape
      "rounded-none",

      // Transitions
      "transition",
      "duration-200",

      // States
      "focus:outline-none focus:shadow-none",

      // Misc
      "relative",
      "cursor-pointer",
      "overflow-hidden overflow-ellipsis",
      "whitespace-nowrap",
      "appearance-none",
    ],
  }),
  trigger: {
    class: [
      // Flexbox
      "flex items-center justify-center",
      "shrink-0",

      // Color and Background
      "bg-transparent",
      "text-surface-500",

      // Size
      "w-12",

      // Shape
      "rounded-tr-md",
      "rounded-br-md",
    ],
  },
  panel: {
    class: [
      // Position
      "absolute top-0 left-0",

      // Shape
      "border-0 dark:border",
      "rounded-md",
      "shadow-md",

      // Color
      "bg-white dark:bg-slate-950",
      "text-surface-800 dark:text-white/80",
      "dark:border-gray-400",
    ],
  },
  wrapper: {
    class: [
      // Sizing
      "max-h-[200px]",

      // Misc
      "overflow-auto",
    ],
  },
  list: {
    class: "py-3 list-none m-0",
  },
  item: ({ context }) => ({
    class: [
      // Font
      "font-normal",
      "leading-none",

      // Position
      "relative",

      // Shape
      "border-0",
      "rounded-none",

      // Spacing
      "m-0",
      "p-2.5",

      // Color
      { "text-surface-700 dark:text-white/80": !context.focused && !context.selected && !context.disabled },
      { "text-surface-600 dark:text-white/70": !context.focused && !context.selected && context.disabled },
      { "bg-gray-100 dark:bg-gray-600/60 text-surface-700 dark:text-white/80": context.focused && !context.selected },
      { "bg-blue-100 dark:bg-blue-400/40  dark:text-white/80": context.focused && context.selected },
      { "bg-blue-50 dark:bg-blue-400/40 dark:text-white/80": !context.focused && context.selected },

      //States
      { "hover:bg-surface-100 dark:hover:bg-surface-600/80": !context.focused && !context.selected },
      { "hover:text-surface-700 hover:bg-surface-100 dark:hover:text-white dark:hover:bg-surface-600/80": context.focused && !context.selected },
      "focus-visible:outline-none focus-visible:outline-offset-0 focus-visible:ring focus-visible:ring-inset focus-visible:ring-blue-400/50 dark:focus-visible:ring-blue-300/50",

      // Transitions
      "transition-shadow",
      "duration-200",

      // Misc
      { "pointer-events-none cursor-default": context.disabled },
      { "cursor-pointer": !context.disabled },
      "overflow-hidden",
      "whitespace-nowrap",
    ],
  }),
  itemgroup: {
    class: [
      //Font
      "font-bold",

      // Spacing
      "m-0",
      "p-2.5",

      // Color
      "text-surface-800 dark:text-white/80",
      "bg-surface-0 dark:bg-surface-600/80",

      // Misc
      "cursor-auto",
    ],
  },
  emptymessage: {
    class: [
      // Font
      "leading-none",

      // Spacing
      "p-2.5",

      // Color
      "text-surface-800 dark:text-white/80",
      "bg-transparent",
    ],
  },
  header: {
    class: [
      // Spacing
      "p-2.5",
      "m-0",

      //Shape
      "border-b",
      "rounded-tl-md",
      "rounded-tr-md",

      // Color
      "text-surface-700 dark:text-white/80",
      "bg-surface-100 dark:bg-surface-800",
      "border-surface-300 dark:border-surface-700",
    ],
  },
  filtercontainer: {
    class: "relative",
  },
  filterinput: {
    class: [
      // Font
      "font-sans",
      "leading-none",

      // Sizing
      "pr-7 py-2 px-2",
      "-mr-7",
      "w-full",

      //Color
      "text-surface-700 dark:text-white/80",
      "bg-gray-50 dark:bg-gray-600",
      "border-surface-200 dark:border-surface-700",

      // Shape
      "border",
      "rounded-sm",
      "appearance-none",

      // Transitions
      "transition",
      "duration-200",

      // States
      "outline-none focus:border-blue-700",

      // Misc
      "appearance-none",
    ],
  },
  filtericon: {
    class: ["absolute", "top-1/2 right-3", "-mt-2"],
  },
  clearicon: {
    class: [
      // Color
      "text-surface-500",

      // Position
      "absolute",
      "top-1/2",
      "right-12",

      // Spacing
      "-mt-2",
    ],
  },
  transition: {
    enterFromClass: "opacity-0 scale-y-[0.8]",
    enterActiveClass: "transition-[transform,opacity] duration-[120ms] ease-[cubic-bezier(0,0,0.2,1)]",
    leaveActiveClass: "transition-opacity duration-100 ease-linear",
    leaveToClass: "opacity-0",
  },
}
</script>
