<template>
  <div>
    <v-h1 back-to="/overview">Edit profile</v-h1>

    <v-info
      v-if="user.data?.kyc_status !== KYCStatus.Ok"
      class="mb-5"
    >
      Missing fields (except email, address and phone) can be automatically populated after your
      <NuxtLink to="/verify-identity">identity verification</NuxtLink>
    </v-info>
    <v-info
      v-if="user.data?.kyc_status !== KYCStatus.NotVerified"
      class="mb-5"
      type="warning"
    >
      If you will change any of your personal information except email, address and phone, new identity verification will be required
    </v-info>

    <div class="flex flex-col items-stretch gap-5">
      <div class="grid grid-cols-[repeat(4,1fr)_40%] gap-5">
        <v-label label="Name">
          <v-input v-model="form.name" />
        </v-label>

        <v-label label="Surname">
          <v-input v-model="form.surname" />
        </v-label>

        <v-label label="Email">
          <v-input v-model="form.email" />
        </v-label>

        <v-label label="Phone (international format)">
          <v-input v-model="form.phone" />
        </v-label>

        <v-label label="Address">
          <v-input v-model="form.address" />
        </v-label>
      </div>

      <div class="grid grid-cols-[auto_200px_200px_1fr] gap-5">
        <v-label label="Sex">
          <v-toggle-buttons
            v-model="form.sex"
            :options="[
              { label: 'Male', value: 'M' },
              { label: 'Female', value: 'F' },
            ]"
          />
        </v-label>

        <v-label label="Birthday date">
          <v-date-input v-model="form.birthday_date" />
        </v-label>

        <v-label label="Nationality">
          <v-dropdown
            v-model="form.nationality_country_code"
            class="w-full"
            placeholder="Select country"
            filter
            :options="countryOptions"
          />
        </v-label>
      </div>

      <div class="grid auto-cols-fr grid-flow-col gap-5">
        <v-label label="Document type">
          <v-dropdown
            v-model="form.document_type"
            class="w-full"
            placeholder="Select document type"
            :options="documentTypeOptions"
          />
        </v-label>

        <v-label label="Document country of issue">
          <v-dropdown
            v-model="form.document_country_code"
            class="w-full"
            placeholder="Select country"
            filter
            :options="countryOptions"
          />
        </v-label>

        <v-label label="Document number">
          <v-input v-model="form.document_number" />
        </v-label>

        <v-label label="Issuance date">
          <v-date-input v-model="form.document_issuance_date" />
        </v-label>

        <v-label label="Expiration date">
          <v-date-input v-model="form.document_expiration_date" />
        </v-label>

        <v-label label="Tax (personal) number">
          <v-input v-model="form.tax_number" />
        </v-label>
      </div>

      <v-button
        class="self-start"
        variant="primary"
        :loading="isSubmitting"
        @click="submit"
      >
        Save
      </v-button>
    </div>
  </div>
</template>

<script setup lang="ts">
import type { Payload } from "core/types/api"
import { countries } from "countries-list"
import type { DropdownOption } from "core/components/v-dropdown.vue"
import { DocumentType, KYCStatus } from "~/types/api"

const form = reactive<Payload<typeof editPublisherProfile>>({
  email: "",
  name: "",
  surname: "",
  phone: "",
  birthday_date: "",
  document_country_code: "",
  document_expiration_date: "",
  document_issuance_date: "",
  document_number: "",
  document_type: null,
  tax_number: "",
  nationality_country_code: "",
  sex: null,
  address: "",
})

const user = useUserStore()
function fillForm() {
  if (!user.data) {
    return
  }

  for (const key in form) {
    if (!(key in user.data)) {
      continue
    }

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    form[key as keyof typeof form] = user.data[key as keyof typeof user.data] as never
  }
}

fillForm()

const isSubmitting = ref(false)
async function submit() {
  isSubmitting.value = true

  try {
    await editPublisherProfile(form)
    await user.getData()
    navigateTo("/overview")

    const { notifySuccess } = useNotification()
    notifySuccess("Profile information was edited")
  } finally {
    isSubmitting.value = false
  }
}

const countryOptions: DropdownOption<string>[] = Object.entries(countries).map(([code, country]) => ({
  label: country.name,
  value: code,
}))

const documentTypeOptions: DropdownOption<DocumentType>[] = Object.values(DocumentType).map((type) => ({
  label: getDocumentTypeName(type),
  value: type,
}))
</script>
