<template>
  <Calendar
    v-model="value"
    date-format="yy-mm-dd"
    show-button-bar
    :manual-input="false"
    :pt="preset"
  />
</template>

<script setup lang="ts">
import Calendar from "primevue/calendar"
import { isEqual } from "lodash-es"
import preset from "core/assets/primevue-presets/calendar"
import { format as formatDate } from "date-fns"

const props = defineProps<{
  modelValue?: null | string
}>()

const emit = defineEmits<{
  (e: "update:modelValue", value?: null | string): void
}>()

const value = ref<null | Date>()
watchImmediate(
  () => props.modelValue,
  () => {
    if (isEqual(props.modelValue, value.value)) {
      return
    }

    if (typeof props.modelValue === "string") {
      value.value = new Date(props.modelValue)
      return
    }

    value.value = props.modelValue
  }
)

watchEffect(() => {
  if (value.value instanceof Date) {
    return emit("update:modelValue", formatDate(value.value, "yyyy-MM-dd"))
  }

  emit("update:modelValue", value.value)
})
</script>
